*{
  margin: 0;
  padding: 0;
  border: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
:root{
  --primary-hue:270;
  --black-lightness:44%;
  --light-lightness:94%;
  --white-lightness:100%;
  --color-primary:#0D3F54;
  --color-secondary:#E5FAEC;
  --color-btn:#FFB904;
  --color-white: hsl(0, 5%, var(--white-lightness));
  --color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
  --color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
  --color-dark: hsl (var(--primary-hue), 5%, var(--dark-lightness));


  /* GENERAL CONTAINER VARIABLES */
  --container-width-lg: 70%;
  --container-width-md: 90%;
  /* BORDER RADIUS VARIABLES */
  --border-radius-1: 1.2rem;
  --border-radius-2: 0.8rem;
  --border-radius-3: 0.5rem;
  /* GENERAL TRANSITION VARIABLE */
  --transition : all 500ms ease;
}
html {
  scroll-behavior: smooth;
  }
  body {

    font-weight: 300;
  line-height: 3;
  }
  body p {
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    font-size: 22px !important;
    line-height: 1.6 !important;
  }

/* GENERAL CONTAINER CLASS */
.container {
  width: var(--container-width-lg);
  margin-inline: auto;
  max-width: 1920px;
  }
  /* GENERAL SECTION STYLES */
section {
  padding: 9rem 0;
  background: white;
  transition: var(--transition);
}
section:nth-child(even) {
  background:var(--color-secondary);
}
  section > h2 {
  text-align: center;
}
section > p {
   text-align: center;
  margin: 0.5rem auto 4rem;
  width: 40%; 
}
/* GENERAL SECTION STYLES*/

a {
  color: var(--color-white);
  transition: var(--transition);
}
/* GENERAL HEADING STYLES*/
h2, h3, h4, h5, h6{
  line-height: 1.1;
  color: var(--color-primary);
}
h1 {
  font-size: 3.5rem  !important;
  color: var(--color-btn);
  font-family: 'Playfair Display', serif;
  font-weight: 800;
  letter-spacing: 0.1em !important;
}
h2{
font-size: 2rem;
font-family: 'Playfair Display', serif;
letter-spacing: 0.1em !important;
font-weight: 600;
}
h3{
  font-size: 1.6rem;
  font-family: 'Playfair Display', serif;
letter-spacing: 0.1em !important;
font-weight: 600;
}
h4{
  font-size: 2rem;
font-family: 'Playfair Display', serif;
}
h5{
  font-size: 0.9rem;
}
h6{
  font-size: 0.8rem;
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}
/* buttons */
.btn{
  display: inline-block;
  display: flex;
  width: fit-content;
  align-items: center;
  gap: 1rem;
  background: var(--color-light);
  color: var(--color-black);
  padding: 0.6rem 2.5rem;
  border: 2px solid transparent;
  border-radius: var(--border-radius-2);
  cursor: pointer;

}
.btn:hover{
  background-color: transparent;
  border-color: var(--color-light);
  transform: translateY(-0.5rem);
}
.btn.white{
  background: var(--color-white);
}
.btn.white:hover{
  background: transparent;
  border-color: var(--color-white);
}
.btn.primary{
  background: var(--color-btn);
  color: white;
}
.btn.primary:hover{
  background: transparent;
  border-color: var(--color-primary);
  color: var(--color-primary);
}
.btn.secondary{
  background: transparent;
  border-color: var(--color-primary);
  color: var(--color-primary);
}
.btn.sm{
  padding: 0.5rem 1.2rem;
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 900px) {
  body {
  font-size: 1rem;
  }
  .container {
  width: var(--container-width-md);
  }
  section {
      padding: 8rem 0;
  }
  section > p {
      width: 72%;
      margin-bottom: 3rem;
  }
  h1 {
  font-size: 2rem;
  }
}
/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
  section {
      padding: 8rem 0;
      background: white;
      transition: var(--transition);
  }

  h2 {
    font-size: 1.2rem;
  }
  h3 {
  font-size: 1.4rem;
  }
  h4 {
  font-size: 1rem;
  }
  p{
    
    font-size: 17px!important;
  }
  .btn{
      padding:0.6rem 1.5rem;
  }
  section {
    padding: 4rem 0;

  }
  section >p{
      width:var(--container-width-md)
  }
}